body {
  height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 400% 400%;
  animation: gradient 10s infinite;
  position: relative;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.back-to-menu {
  color: #fff;
  align-items: center;
  padding: 20px 30px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.back-to-menu:before {
  content: "";
  width: 56px;
  height: 56px;
  background: rgba(30, 233, 186, .3);
  border-radius: 30px;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.back-to-menu span {
  letter-spacing: .25rem;
  text-transform: uppercase;
  font-weight: 500;
  line-height: 1;
  position: relative;
}

.back-to-menu svg {
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke: #fff;
  stroke-width: 2px;
  margin-left: 10px;
  transition: all .3s;
  position: relative;
  top: 0;
  transform: translateX(-5px);
}

.back-to-menu:hover {
  color: #000;
}

.back-to-menu:hover:before {
  width: 200px;
  background: #1ee9ba;
}

.back-to-menu:hover svg {
  stroke: #000;
  transform: translateX(0);
}

.back-to-menu:active {
  transform: scale(.95);
}

